/*global chrome*/

import {gmButton, gnButton} from "./tweet/gmButton";
import {replyButtons} from "./tweet/replyButtons";
import {addMemeButton, addReloadContent} from "./tweet/editableContent";

chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
    if (message.status && 'complete' === message.status) {
        setTimeout(() => {
            gmButton();
            gnButton();
        }, 500);
    }
});

const callback = (mutationsList, observer) => {
    replyButtons();
    addMemeButton();
    addReloadContent();
};

const targetNode = document.body;
const config = { attributes: false, childList: true, subtree: true };
const observer = new MutationObserver(callback);
observer.observe(targetNode, config);
