const addButtonIntoReply = (id, label) => {
    const modalBox = document.querySelector("[aria-labelledby='modal-header']");

    if (modalBox) {
        const button = modalBox.querySelector(`#${id}`);

        if (!button) {
            const geolocButton = modalBox.querySelector("[data-testid='geoButton']");
            if (geolocButton) {
                const buttonContent = document.createElement('div');
                buttonContent.id = id;
                buttonContent.innerText = label;
                buttonContent.style.width           = '150%';
                buttonContent.style.textAlign       = 'center';
                buttonContent.style.backgroundColor = 'rgb(233, 110, 195)';
                buttonContent.style.color           = 'rgb(255,255,255)';
                buttonContent.style.borderRadius    = '15px';
                buttonContent.style.padding         = '5px 7px 5px 7px';
                buttonContent.style.marginRight     = '5px';
                buttonContent.style.cursor          = 'pointer';
                buttonContent.style.fontSize        = '.9em';

                buttonContent.setAttribute('role', 'button');

                geolocButton.parentNode.appendChild(buttonContent);

                return buttonContent;
            }
        }
    }

    return null;
}

export {addButtonIntoReply}
