import {newGm, newGn} from "../utils/getGmText";
import {getLastChild} from "../utils/getLastChild";

const gmElementId = 'new_gm';
const gnElementId = 'new_gn';

const addButton = (label, content, id) => {
    let button = document.getElementById(id);
    if (!button) {
        const newTweetElement = document.querySelector("header[role='banner']")?.querySelector("a[href='/compose/tweet']")?.parentElement;

        if (newTweetElement) {
            button = newTweetElement.cloneNode(true);
            button.id = id;

            const linkElement = button.querySelector("a[href='/compose/tweet']");
            linkElement.href = `/compose/tweet?text=${content}`;
            linkElement.setAttribute('aria-label', id);
            linkElement.setAttribute('data-testid', id);

            const buttonLabel = getLastChild(button);
            buttonLabel.innerText = label;

            newTweetElement.parentNode.appendChild(button);
        }
    }
}

const gmButton = () => {
    addButton('GM ðŸŒž', newGm(), gmElementId);
}

const gnButton = () => {
    addButton('GN ðŸŒ›', newGn(), gnElementId);
}

export {gmButton, gnButton};
