import {addButtonIntoReply} from "../utils/reply";
import {getReplyContentFromType} from "../utils/getGmText";
import {dataGmType} from "./replyButtons";

const getModalBox = () => {
    return document.querySelector("[aria-labelledby='modal-header']");
}

const addReloadContent = () => {
    const reloadButton = addButtonIntoReply('reload-content', 'Reload');

    if (reloadButton) {
        const modalBox = getModalBox();

        reloadButton.addEventListener('click', (event) => {
            const editableContent = modalBox.querySelector("[data-testid='tweetTextarea_0']");
            editableContent.focus();

            window.setTimeout(() => {
                let sel, range;

                if (window.getSelection && document.createRange) {
                    range = document.createRange();
                    range.selectNodeContents(editableContent);
                    sel = window.getSelection();
                    sel.removeAllRanges();
                    sel.addRange(range);
                } else if (document.body.createTextRange) {
                    range = document.body.createTextRange();
                    range.moveToElementText(editableContent);
                    range.select();
                }
            }, 1);

            window.setTimeout(() => {
                const dataTransfer = new DataTransfer();
                dataTransfer.setData('text/plain', getReplyContentFromType(editableContent.getAttribute(dataGmType)));

                const event = new ClipboardEvent('paste', {
                    clipboardData: dataTransfer,
                    bubbles: true
                });
                editableContent.dispatchEvent(event);
            }, 50);
        });
    }
}

const addMemeButton = () => {
    const memeButton = addButtonIntoReply('gmeme', 'GMeme');

    if (memeButton) {
        const modalBox = getModalBox();

        memeButton.addEventListener('click', () => {
            const media = modalBox.querySelector("[aria-label='Media']");
            if (media) {
                const closeMediaButton = media.querySelector("[role='button']");
                if (closeMediaButton) {
                    closeMediaButton.click();
                }
            }

            const numbers = Array.from({length: 36}, (_, i) => i + 1);
            const randNumber = numbers[Math.floor(Math.random() * numbers.length)];

            const url = ``;
            const editableContent = modalBox.querySelector("[data-testid='tweetTextarea_0']");

            fetch(url)
                .then((response) => response.blob())
                .then((blob) => {
                    const dataTransfer = new DataTransfer();
                    dataTransfer.items.add(new File([blob], 'image.png', {type: 'image/png'}));

                    const event = new ClipboardEvent('paste', {
                        clipboardData: dataTransfer,
                        bubbles: true
                    });
                    editableContent.dispatchEvent(event);
                })
        })
    }
}

export {addReloadContent, addMemeButton}
