const getIcons = (mergeWith) => {
    return icons.concat(mergeWith);
}

const getReplyContentFromType = (type, withName = null) => {
    return type === gnClassName ? replyGn(withName) : replyGm(withName);
}

const getGm = () => {
    const data = [0, 1];
    return 1 === data[Math.floor(Math.random() * data.length)] ? 'gm' : 'GM';
}

const getGn = () => {
    const data = [0, 1];
    return 1 === data[Math.floor(Math.random() * data.length)] ? 'gn' : 'GN';
}

const getRandomItem = (data) => {
    return data[Math.floor(Math.random() * data.length)];
}

const newGm = () => {
    return getContent(getGm(), getRandomItem(newContent.concat(newGmContent)), getRandomItem(getIcons(gmIcons)));
}

const newGn = () => {
    return getContent(getGn(), getRandomItem(newContent.concat(newGnContent)), getRandomItem(getIcons(gnIcons)));
}

const replyGm = (withName = null) => {
    const second = null !== withName ? withName : getRandomItem(replyContent.concat(replyGmContent))
    return getContent(getGm(), second, getRandomItem(icons));
}

const replyGn = (withName = null) => {
    const second = null !== withName ? withName : getRandomItem(replyContent.concat(replyGnContent))
    return getContent(getGn(), second, getRandomItem(icons));
}

const getContent = (gm, content, icon) => {
    let second = '' === content ? content : ` ${content}`;
    const third  = '' === icon ? icon : ` ${icon}`;

    return `${gm}${second}${third}`;
}

export {newGm, newGn, replyGm, replyGn, getReplyContentFromType}
